AA","sourcesContent":["@import \"../../style/all\";\n\n@keyframes show {\n    0% {\n        opacity: 0;\n        margin-top: 0px;\n        padding: 0 15px 0;\n        line-height: 0;\n        max-height: 0;\n    }\n    100% {\n        opacity: 1;\n        margin-top: 0px;\n        padding: 8px 15px 15px;\n        line-height: inherit;\n        max-height: 1000px;\n    }\n}\n\n@keyframes hide {\n    100% {\n        opacity: 0;\n        margin-top: 0px;\n        padding: 0 15px 0;\n        line-height: 0;\n        max-height: 0;\n    }\n    0% {\n        opacity: 1;\n        margin-top: 0px;\n        padding: 8px 15px 15px;\n        line-height: inherit;\n        max-height: 1000px;\n    }\n}\n\n\n@include breakpoint(phablet) {\n    @keyframes show {\n        0% {\n            opacity: 0;\n            margin-top: 0px;\n            padding: 0 30px 0;\n            line-height: 0;\n            max-height: 0;\n        }\n        100% {\n            opacity: 1;\n            margin-top: 0px;\n            padding: 8px 30px 15px;\n            line-height: inherit;\n            max-height: 1000px;\n        }\n    }\n    @keyframes hide {\n        100% {\n            opacity: 0;\n            margin-top: 0px;\n            padding: 0 30px 0;\n            line-height: 0;\n            max-height: 0;\n        }\n        0% {\n            opacity: 1;\n            margin-top: 0px;\n            padding: 8px 30px 15px;\n            line-height: inherit;\n            max-height: 1000px;\n        }\n    }\n}\n\n:host {\n    background-color: $light-gray;\n}\n    .panel {\n        @include appearance(none);\n        @include border-radius(0);\n        font-family: $default-font;\n        color: $default-text;\n        padding: 15px 15px;\n        display: flex;\n        align-items: center;\n        position: relative;\n        text-decoration: none;\n        overflow-wrap: break-word;\n        line-height: 1.7;\n        cursor: pointer;\n        border: none;\n        width: 100%;\n      